v, section {\r\n// \tdisplay: block;\r\n// }\r\n// body {\r\n// \tline-height: 1;\r\n// }\r\n// ol, ul {\r\n// \tlist-style: none;\r\n// }\r\n// blockquote, q {\r\n// \tquotes: none;\r\n// }\r\n// blockquote:before, blockquote:after,\r\n// q:before, q:after {\r\n// \tcontent: '';\r\n// \tcontent: none;\r\n// }\r\n// table {\r\n// \tborder-collapse: collapse;\r\n// \tborder-spacing: 0;\r\n// }\r\nbody{\r\n\tbox-sizing: border-box;\r\n\tmargin: 0;\r\n\tpadding: 0;\r\n}","\r\n\r\nbody{\r\n    //background-color: $bgColor;\r\n    // background-image: linear-gradient($gradientOuter, $hamburger, $gradientOuter);\r\n    background-color: #326765;\r\n    background-image:url(\"https://www.transparenttextures.com/patterns/black-linen.png\"); \r\n    //url(\"https://www.transparenttextures.com/patterns/hexellence.png\");\r\n    //url(\"https://www.transparenttextures.com/patterns/binding-dark.png\");\r\n    //url(\"https://www.transparenttextures.com/patterns/black-thread.png\");\r\n    //url(\"https://www.transparenttextures.com/patterns/darth-stripe.png\");\r\n    //url(\"https://www.transparenttextures.com/patterns/diagmonds.png\");\r\n    font-family: $bodyFont;\r\n    color: $fontColor;\r\n}\r\n\r\n","//$bgColor: #f8f8e6;\r\n//$bgColor: #FFF0A5;\r\n//$bgColor: #FFF0A5;\r\n//Zelena boja u gradijentu pozadine\r\n$gradientOuter: #326765;\r\n$btnGradientLight: #7DA87B;\r\n//$mainGreenColor: #4a916a;\r\n//$mainYellowColor: #ffca05;\r\n//boja fonta i hamburger menija\r\n$fontColor: #27253D;\r\n//$hamburger: #FFB03B;\r\n//$hamburger: #468966;\r\n//boja zaglavlja na malim ekranima\r\n$hamburger: #27253D;\r\n//boja pozadine menija na malim ekranima\r\n$menuBG: rgba(255, 255, 255, .97);\r\n//boja fonta aktivne klase\r\n$fontAktiv: #fff;\r\n$fontLight: #F5F5C6;\r\n$cardShedow: #000;\r\n//boja pozadine na kartici\r\n$cardTextBg: #f5f5c685;\r\n$contactBg: #27253d9d;\r\n\r\n$bodyFont: 'Montserrat', sans-serif;\r\n$titleFont: 'Sriracha', cursive;\r\n\r\n// font-family: 'Kalam', cursive;\r\n// font-family: 'Marck Script', cursive;\r\n// font-family: 'Montserrat', sans-serif;\r\n// font-family: 'Sriracha', cursive;\r\n//backgroundColor: #F5F5C6;","\r\n.homeImgContainer{\r\n    display: flex;\r\n    justify-content: center;\r\n    min-height: calc(100vh - 50px);\r\n}\r\n// img from bacgroundScreen / homescreen\r\n.homeImg{\r\n    width: 316px;\r\n    // min-width: 230px;\r\n    height: 60vh;\r\n    margin: 110px 0 20px;\r\n    border-radius: 8px;\r\n    box-shadow: 4px 4px 8px 5px rgba(0, 0, 0, 0.6);\r\n    z-index: -5;\r\n    object-fit: cover;\r\n}\r\n.homeImgBig{\r\n    display: none;\r\n}\r\n\r\n.homeAnima-appear{transform:scale(0.8); opacity: 0.05;}\r\n.homeAnima-appear-active{transform:scale(1); opacity: 1; transition: opacity 1200ms, transform 500ms cubic-bezier(0.16, 1, 0.3, 1);}\r\n// .homeAnima-appear{opacity: 0.25;}\r\n// .homeAnima-appear-active{opacity: 1; transition: opacity 1200ms}\r\n.homeAnima-appear-done{}\r\n\r\n.SocialMediaTable{\r\n    position: absolute;\r\n    top: 40vh;\r\n    right: 0;\r\n}\r\n.SocialMediaTable td{\r\n    padding: 5px;\r\n}\r\n@media only screen and (min-width: 770px) {\r\n    .homeImg{\r\n        display: none;\r\n    }\r\n    .homeImgBig{\r\n        display: block;\r\n        width: 700px;\r\n        z-index: -5;\r\n        object-fit: contain;\r\n        height: 60vh;\r\n        margin: 110px 0 20px;\r\n        border-radius: 10px;\r\n        object-fit: cover;\r\n    }\r\n}\r\n","\r\n// NAVIGATION\r\n\r\n//nav sm-screen\r\n.manuSm{\r\n    width: 200px;\r\n    z-index: 11;\r\n    \r\n    // background-color: $menuBG;\r\n    // height: calc(100vh - 110px);\r\n    // display: block;\r\n    // padding-top: 5px;\r\n    // // width: 100%;\r\n    // position: fixed;\r\n    // top: 110px;\r\n    // left: -200px;\r\n    // background-color: $menuBG;\r\n    // // height: calc(80vh - 110px);\r\n    // height: calc(60vh - 5px);\r\n    // // z-index: 1000;\r\n   \r\n}\r\n\r\n//Animation of small screen\r\n.manuSm-enter{}\r\n.manuSm-enter-active{transform: translateX(200px); \r\n    transition: transform 500ms cubic-bezier(0.34, 1.56, 0.64, 1);}\r\n.manuSm-enter-done{transform: translateX(200px)}\r\n\r\n.manuSm-exit{transform: translateX(200px)}\r\n.manuSm-exit-active{ opacity: 0; transform: translateX(-200px); \r\n    transition: opacity 800ms, transform 500ms cubic-bezier(0.7, 0, 0.84, 0);}\r\n.manuSm-exit-done{transform: translateX(-200px);}\r\n\r\n//menu on big screen\r\n.menuBig{\r\n    display: none;\r\n}\r\n.menuBacground{\r\n    // height: calc(100vh - 110px);\r\n    // width: calc(100vw - 220px);\r\n    height: calc(100vh - 60px);\r\n    width: 100vw;\r\n    position: fixed;\r\n    top: 60px;\r\n    // left: 200px;\r\n    z-index: 10;\r\n    // background-color: chartreuse;\r\n    \r\n}\r\nnav{\r\n    display: block;\r\n    padding-top: 5px;\r\n    // width: 100%;\r\n    position: fixed;\r\n    top: 110px;\r\n    left: -200px;\r\n    background-color: $menuBG;\r\n    // height: calc(80vh - 110px);\r\n    height: calc(60vh - 5px);\r\n}\r\n.link, .subLink{\r\n    padding: 10px;\r\n    display: block;\r\n    text-decoration: none;\r\n    font-weight: bold;\r\n    text-align: center;\r\n    border-bottom: 1px solid $hamburger;\r\n    width: 75%;\r\n    margin: 0px auto;\r\n}\r\n.link:hover:not(.navActiveLink){\r\n    background-color: $gradientOuter;\r\n    color: $fontAktiv;\r\n}\r\n// .first, .secund, .third{\r\n//     display: none;\r\n// }\r\n.logoInMenu{\r\n    display: none;\r\n}\r\n.navActiveLink{\r\n    background-color: $hamburger;\r\n    color: $fontAktiv;\r\n}\r\n    //subMenu\r\n\r\n.productsMenu{\r\n    width: 100%;\r\n}\r\n.subLink{\r\n    background:linear-gradient(to bottom, $btnGradientLight 5%, $gradientOuter 100%);\r\n    color: $fontLight;\r\n}\r\n.subLink:hover:not(.subActiveLink){\r\n    background:linear-gradient(to bottom, $fontLight 5%, $btnGradientLight 100%);\r\n    color: $gradientOuter;\r\n}\r\n.subActiveLink{\r\n    background: none;\r\n    background-color: $fontLight;\r\n    color: $hamburger;\r\n}\r\n//submenu animation\r\n.productsMenu-enter{transform: scaleX(0.3); opacity: 0.2;}\r\n.productsMenu-enter-active{transform: scaleX(1); opacity: 1; transition: opacity 800ms, transform 500ms cubic-bezier(0, 0.55, 0.45, 1);}\r\n.productsMenu-enter-done{transform: scaleX(1); opacity: 1;}\r\n\r\n.productsMenu-exit{ opacity: 1}\r\n.productsMenu-exit-active{opacity: 0; transition: opacity 800ms;}\r\n.productsMenu-exit-done{opacity: 0;}\r\n\r\n@media only screen and (min-width: 481px) {\r\n    \r\n    //menu links style changed\r\n    .link{\r\n        background-color: $hamburger;\r\n        color: $fontLight;\r\n    }\r\n    .navActiveLink{\r\n        background-color: $fontLight;\r\n        color: $gradientOuter;\r\n    }\r\n    .menuBacground{\r\n        display: none;\r\n    }\r\n    .manuSm{\r\n        background-color: $hamburger;\r\n        width: 100%;\r\n        display: inline-block;\r\n        height: 50px;\r\n        position: fixed;\r\n        top: 0px;\r\n        left: 0px;\r\n        padding: 0;\r\n    }\r\n    .manuSm a.link, .subLink{\r\n        // height: 50px;\r\n        display: inline-block;\r\n        width: 110px;\r\n        padding: 0;\r\n        // border: none;\r\n        line-height: 50px;\r\n        float: left;\r\n    }\r\n    //disabling menu animation on bigger screens\r\n    .manuSm-enter{transform: translateX(0px)}\r\n    .manuSm-enter-active{transform: translateX(0px)}; \r\n    .manuSm-enter-done{transform: translateX(0px)}\r\n\r\n    .manuSm-exit{transform: translateX(0px)}\r\n    .manuSm-exit-active{ transform: translateX(0px)}\r\n    .manuSm-exit-done{transform: translateX(0px)}\r\n\r\n    .subLink{\r\n        display: block;\r\n        position: absolute;\r\n        top: 50px;\r\n        left: 110px;\r\n        // width: 180px;\r\n        width: 100%;\r\n        background:linear-gradient(to bottom, $gradientOuter 1%, $hamburger 5%, $gradientOuter );\r\n        color: $fontLight;\r\n\r\n    }\r\n    .subLink:hover:not(.subActiveLink){\r\n        background:linear-gradient(to bottom, $fontLight 5%, $btnGradientLight 100%);\r\n        color: $gradientOuter;\r\n    }\r\n    .subActiveLink{\r\n        background: none;\r\n        background-color: $fontLight;\r\n        color: $hamburger;\r\n    }\r\n    \r\n    .first{\r\n        position: absolute;\r\n        top: 50px;\r\n        // left: 75px;\r\n        left: 0;\r\n        \r\n        \r\n    }\r\n    .secund{\r\n        position: absolute;\r\n        top: 100px;\r\n        // left: 75px;\r\n        left: 0;\r\n        \r\n    }\r\n    .third{position: absolute;\r\n        top: 150px;\r\n        // left: 75px;\r\n        left: 0;\r\n        border-bottom: none;\r\n    \r\n    }\r\n    \r\n}    \r\n@media only screen and (min-width: 770px) {\r\n    //changing back the becgraund stiye for submenu links (subLinks)\r\n    // .subLink{\r\n    //     background:linear-gradient(to bottom, $btnGradientLight 5%, $gradientOuter 100%);\r\n    //     color: $fontLight;\r\n    // }\r\n    // .subLink:hover:not(.subActiveLink){\r\n    //     background:linear-gradient(to bottom, $fontLight 5%, $btnGradientLight 100%);\r\n    //     color: $gradientOuter;\r\n    // }\r\n    // .subActiveLink{\r\n    //     background: none;\r\n    //     background-color: $fontLight;\r\n    //     color: $hamburger;\r\n    // }\r\n    .manuSm a.link, .subLink{\r\n        width: 180px;\r\n    }\r\n    .subLink{\r\n        display: block;\r\n        position: absolute;\r\n        top: 50px;\r\n        left: 180px;\r\n    }\r\n    .subLink.first{\r\n        position: absolute;\r\n        top: 50px;\r\n        left: 180px;\r\n        \r\n    }\r\n    .secund{\r\n        position: absolute;\r\n        top: 100px;\r\n        left: 180px;\r\n        \r\n    }\r\n    .third{position: absolute;\r\n        top: 150px;\r\n        left: 180px;\r\n        border-radius: 0 0 10px 10px;\r\n    }\r\n}\r\n","// Styled Heading component\r\n.title{\r\n    display: none;\r\n    padding-top: 15px;\r\n    width: 80%;\r\n    margin: 0 auto 20px;\r\n    text-align: center;\r\n}\r\n.headingTitle{\r\n    font-family: $titleFont;\r\n}\r\n\r\n.titleImg{\r\n    border-radius: 10%;\r\n    margin: auto;\r\n}\r\n\r\n","//styled komponent from hamburger menu - NavigationSM component\r\n\r\n//reading - with hamburger menu\r\n.hamburgerContainer{\r\n    height: 60px;\r\n    background-color: $hamburger;\r\n    position: fixed;\r\n    top: 0;\r\n    left: 0;\r\n    margin-bottom: 20px;\r\n    width: 100%;\r\n    z-index: 1005;\r\n}\r\n.smallScr{\r\n    float: left;\r\n    position: absolute;\r\n    left: 20px;\r\n    top: 6px;\r\n    background-color: white;\r\n    padding: 8px;\r\n    border-radius: 5px;\r\n    cursor: pointer;\r\n    height: 30px;\r\n}\r\n.hamburgerMenu{\r\n    height: 40px;\r\n    float: right;\r\n    filter: invert(100%);\r\n    position: absolute;\r\n    top: 10px;\r\n    right: 20px;\r\n    cursor: pointer;\r\n}\r\n@media only screen and (min-width: 481px) {\r\n    .hamburgerContainer, .smallScr, .hamburgerMenu{\r\n        display: none;\r\n    }\r\n}\r\n\r\n\r\n\r\n\r\n\r\n","\r\n.productContainer{\r\n    display: flex;\r\n    flex-direction: row;\r\n    // justify-content: space-between;\r\n    align-items: center;\r\n    // justify-content: space-between;\r\n    box-shadow: 5px 0 8px 3px $fontLight;\r\n    padding: 8px 0;\r\n    margin-top: 80px;\r\n    background-color: $gradientOuter;\r\n}\r\n.productsTitle, .productsTitleRight{\r\n    color: $fontLight;\r\n    margin-left: 30px;\r\n    font-size: 2rem;\r\n    text-shadow: 1px 3px 3px $hamburger;\r\n}\r\n.productsTitleRight{\r\n    margin: 0 30px 0 20px;\r\n}\r\n.productImg{\r\n    \r\n    width: 30%;\r\n    margin-left: 20px;\r\n    border-radius: 100%;\r\n    max-height: 100px;\r\n    max-width: 150px;\r\n}\r\n.productImgRght{\r\n    width: 30%;\r\n    margin-right: 20px;\r\n    border-radius: 100%;\r\n    float: right;\r\n    max-he